import React from 'react'
import imagen1 from "../assets/img/foto2.jpeg";
import imagen2 from "../assets/img/foto3.jpeg";
import video from "../assets/img/Acredita tu idioma y viaja con la US #universidaddesevilla (480p).mp4";
import { Link } from 'react-router-dom';



export const instituto = () => {
  return (
    <div>
      <div class="container">
        <div class="row align-items-start">
          <div class="col">
            En este apartado conoceras mas que todo lo referente la Historia del instituto
          </div>
          <div class="col">
            Este instituto es uno de los mejores institutos que tenemos de la universidad de sevilla
          </div>
          <div class="col">
            la  acalidad  academica  es uno de las mejores en si con docentes expertos  en el dias los cuales tiene una trayectoria de ensenanza
          </div>
        </div>
        <div class="row align-items-center">
          <div class="col">
            Cabe mencionar que los estudiantes que estudian en este instituto tienen un certificado aparte del instituto.
          </div>
          <div class="col">
            Este instituto es uno de los mas recomendados a nivel internacional.
          </div>
          <div class="col">
            dicha acreditacion o sertificado del certificado tiene  una alta validez
          </div>
        </div>

      </div>
      <h2>El instituto aprende nuevos idiomas para la vida donde podrs aprender varios idiomas que sern muy tiles si quisieras estudiar en otros pases o si quieres trabajar en otros pases.</h2>
      <p>AQU TE MOSTRAR LAS IMGENES DEL INSTITUTO IDIOMAS DE LA UNIVERSIDAD SEVILLA</p>

      <div id="carouselExample" className="carousel slide">
        <div className="carousel-inner">
          <div className="carousel-item active">
            <img src={imagen1} className="d-block w-100" width="200" height="400" alt="foto1" />
          </div>
          <div className="carousel-item">
            <img src={imagen2} className="d-block w-100" width="200" height="400" alt="foto2" />
          </div>
          {/* Agregar otros elementos del carousel */}
        </div>
        <button className="carousel-control-prev" type="button" data-bs-target="#carouselExample" data-bs-slide="prev">
          <span className="carousel-control-prev-icon" aria-hidden="true"></span>
          <span className="visually-hidden">Previous</span>
        </button>
        <button className="carousel-control-next" type="button" data-bs-target="#carouselExample" data-bs-slide="next">
          <span className="carousel-control-next-icon" aria-hidden="true"></span>
          <span className="visually-hidden">Next</span>
        </button>
      </div>
      <button>
        <Link to="https://www.cambridge.es/catalogo/primaria">
          Primaria
        </Link>
      </button>
      
      <button>
        <Link to="https://www.conaliteg.sep.gob.mx/secundaria.html">
          Secundaria
        </Link>
      </button>

      <button>
        <Link to="https://www.casadellibro.com/top-libros">
          Libros
        </Link>
      </button>

      <button>
        <Link to="https://www.cambridgeenglish.org/latinamerica/learning-english/">
          Ingles
        </Link>
      </button>
      <button>
        <Link to="https://www.cambridge.es/catalogo/primaria">
          Frances
        </Link>
      </button>
      <br></br>
      <iframe width="560" height="315" src="https://www.youtube.com/embed/UXZwSrzVJfc?si=XsUHgvO1yFSjX6uO" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>

      {/* Formulario de seleccin de idioma */}
      <div className="container">
        <h1>Selecciona un idioma para mejorar tu experiencia y que quieras aprender:</h1>
        <form>
          <div className="form-group">
            <label htmlFor="idiomaSelect">Selecciona un idioma:</label>
            <select className="form-control" id="idiomaSelect">
              <option value="aleman">Alemn</option>
              <option value="espanol">Espaol</option>
              <option value="frances">Francs</option>
              <option value="ingles">Ingls</option>
            </select>
          </div>
          <button type="submit" className="btn btn-primary">Enviar</button>
        </form>
      </div>
    </div>
  );
}







