import React from 'react'
import foto15 from"../assets/img/foto15.webp";
import foto16 from"../assets/img/foto16.jpeg";
import CAPTURA from"../assets/img/Captura de pantalla 2023-12-10 182252.png";

export const Examenes = () => {
    return (
        <div>
            <div class="container">
                <div class="row align-items-start">
                    <div class="col">
                    </div>
                    <div class="col">
                        Este instituto saque un rol de examenes de cada cierto tiempo para que 
                        los estudiantes lo pierdan el hilo y esten aprendiendo correctamente
                    </div>
                    <div class="col">
                        los estudiantes deben estar atentos a la lista de examen al momento de salir el cronograma
                    </div>
                </div>
                <div class="row align-items-center">
                    <div class="col">
                        todos los examenes que se dan en dicho instituto son 40 munitos.     
                    </div>
                    <div class="col">
                        despues de dar dicho examen se  coordina con el  docente la observaciones de dicha examen
                    </div>
                    <div class="col">
                        En caso de algun problema debe ser al  instituto. 
                    </div>
                </div>
            </div>
            <h1>Calendario de exmenes</h1>
            <div id="carouselExample" class="carousel slide">
                <div class="carousel-inner">
                    <div class="carousel-item active">
                        <img src={foto16} class="d-block w-100" width="350" height="550"/>
                    </div>
                    <div class="carousel-item">
                        <img src={foto15} class="d-block w-100" width="350" height="500"/>
                    </div>
                </div>
                <button class="carousel-control-prev" type="button" data-bs-target="#carouselExample" data-bs-slide="prev">
                    <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                    <span class="visually-hidden">Previous</span>
                </button>
                <button class="carousel-control-next" type="button" data-bs-target="#carouselExample" data-bs-slide="next">
                    <span class="carousel-control-next-icon" aria-hidden="true"></span>
                    <span class="visually-hidden">Next</span>
                </button>
            </div>
            <p>Aqu encontrars las fechas, horas y ubicaciones de las convocatorias de exmenes de nuestras pruebas de acreditacin.  Recuerda que para presentarte debes haberte matriculado previamente en la convocatoria en que deseas examinarte.</p>

            <img src={CAPTURA} width={600} height={540}/>

                <p>Debes registrarte en aqui para dar el examen</p>
                <div class="container">
                    <h1>Registro para el Examen</h1>
                    <form id="formularioRegistro">
                        <div class="form-group">
                            <label for="nombre">Nombre:</label>
                            <input type="text" class="form-control" id="nombre" required/>
                        </div>
                        <div class="form-group">
                            <label for="apellido">Apellido:</label>
                            <input type="text" class="form-control" id="apellido" required/>
                        </div>
                        <div class="form-group">
                            <label for="idioma">Idioma del Examen:</label>
                            <select class="form-control" id="idioma" required>
                                <option value="aleman">Alemn</option>
                                <option value="espanol">Espaol</option>
                                <option value="frances">Francs</option>
                            </select>
                        </div>
                        <div class="form-group">
                            <label for="tipoExamen">Tipo de Examen:</label>
                            <select class="form-control" id="tipoExamen" required>
                                <option value="escrito">Escrito</option>
                                <option value="oral">Oral</option>
                            </select>
                        </div>
                        <div class="form-group">
                            <label for="fecha">Fecha del Examen:</label>
                            <input type="date" class="form-control" id="fecha" required/>
                        </div>
                        <button type="submit" class="btn btn-primary">Registrarse para el Examen</button>
                        <br></br>
                        <iframe width="560" height="315" src="https://www.youtube.com/embed/JGMuvGzl1Z4?si=Sk3xGY5i0uDCauHq" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>
                    </form>
                </div>
        </div>
    )
}
