import {Instituto, instituto} from "./componentes/Instituto";
import {NavBar} from "./componentes/Navbar";
import {Secretaria} from "./componentes/Secretaria";
import {Tramites} from "./componentes/Tramites";
import {Examenes} from "./componentes/Examenes";
import {Matriculados} from "./componentes/Matriculados";
import {Biblioteca} from "./componentes/Biblioteca";
import {Cursos} from "./componentes/Cursos";
import {Formulario} from "./componentes/Formulario";
import Footer from "./componentes/Footer";
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { Direccion } from "./componentes/Direccion";
import { Creditos } from "./componentes/Creditos";


function App() {
  return (
    <div className="App">
      <Router>
        <NavBar/>
        <Routes>
          <Route path="/instituto" exact Component={instituto}/>
          <Route path="/Cursos"  exact Component={Cursos}/>
          <Route path="/Examenes" exact Component={Examenes}/>
          <Route path="/direccion" exact Component={Direccion}/>
          <Route path="/secretaria" exact Component={Secretaria}/>
          <Route path="/Biblioteca" exact Component={Biblioteca}/>
          <Route path="/Tramites" exact Component={Tramites}/>
          <Route path="/Matriculados" exact Component={Matriculados}/>
          <Route path="/Formulario" exact Component={Formulario}/>
          <Route path="/Creditos" exact Component={Creditos}/>

        </Routes>
        <Footer/>
        
      </Router>
    </div>
  );  
}
export default App;



