import React from 'react';
import { Link } from 'react-router-dom';
import logo1 from'../assets/img/logo1.png';
import section from'../assets/img/foto1.jpeg';
import FOTO20 from'../assets/img/FOTO20.jpeg';

export const NavBar = () => {
    return (
        <div>
            <header>
                <img src={logo1} alt='Banner' width="250" height="250" className='img-fluid' />
            </header>
            <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
                <div className="container-fluid">
                    <Link className="navbar-brand" to={"/"}>
                        <img src={section} alt='Logo' width={120} height={120} className='img-fluid' />
                        <p className='fst-italic'>Instituto Idiomas</p>
                    </Link>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNav">
                        <ul className="navbar-nav mx-auto navbar-nav-scroll">
                            <li className="nav-item">
                                <Link className="nav-link active" aria-current="true" to={"/secretaria"}><p className='fst-italic fs-4'>Secretaria</p></Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to={"/Cursos"}><p className='fst-italic fs-4'>Cursos</p></Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to={"/Matriculados"}><p className='fst-italic fs-4'>Matriculacion</p></Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to={"/instituto"}><p className='fst-italic fs-4'>instituto</p></Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to={"/Direccion"}><p className='fst-italic fs-4'>Direccion</p></Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to={"/Tramites"}><p className='fst-italic fs-4'>Tramites</p></Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to={"/Examenes"}><p className='fst-italic fs-4'>Examenes</p></Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to={"/Biblioteca"}><p className='fst-italic fs-4'>Biblioteca</p></Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to={"/Creditos"}><p className='fst-italic fs-4'>Creditos</p></Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to={"/Formulario"}><p className='fst-italic fs-4'>Formulario</p></Link>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>
            <img src={FOTO20} width={1350} height={300}/> 
      </div>
)
 
}
