import React from 'react'
import IMG1 from"../assets/img/foto18.jpg";
import IMG2 from"../assets/img/foto17.jpeg";

export const Biblioteca = () => {
    return (
        <div>
            <h1>Biblioteca</h1>
            <div id="carouselExample" class="carousel slide">
                <div class="carousel-inner">
                    <div class="carousel-item active">
                        <img src={IMG1} class="d-block w-100" width="300" height="500"/>
                    </div>
                    <div class="carousel-item">
                        <img src={IMG2} class="d-block w-100" width="300" height="500"/>
                    </div>

                </div>
                <button class="carousel-control-prev" type="button" data-bs-target="#carouselExample" data-bs-slide="prev">
                    <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                    <span class="visually-hidden">Previous</span>
                </button>
                <button class="carousel-control-next" type="button" data-bs-target="#carouselExample" data-bs-slide="next">
                    <span class="carousel-control-next-icon" aria-hidden="true"></span>
                    <span class="visually-hidden">Next</span>
                </button>
            </div>
            <p>aqui  podras encontrar diversos tipos  libros</p>
            <h1>TE LO MOSTRAREMOS COMO ENLACES</h1>
            <h1>Libros que se tiles para aprendas ingls</h1>
            <ul>
                <li><a href="https://www.cambridge.org/grammarinuse/" target="_blank">English Grammar in Use by Raymond Murphy</a></li>
                <li><a href="https://www.amazon.com/Word-Power-Made-Easy-Lewis/dp/8183071007" target="_blank">Word Power Made Easy by Norman Lewis</a></li>
                <li><a href="https://www.amazon.com/Essential-Idioms-English-Robert-Dixson/dp/0135820253" target="_blank">Essential Idioms in English by Robert J. Dixson</a></li>
                <li><a href="https://www.amazon.com/Elements-Style-Fourth-William-Strunk/dp/020530902X" target="_blank">The Elements of Style by William Strunk Jr. and E.B. White</a></li>
                <li><a href="https://www.oxfordlearnersdictionaries.com/" target="_blank">Oxford Advanced Learner's Dictionary</a></li>
                <li><a href="https://www.cambridge.org/phrasalverbsinuse/" target="_blank">English Phrasal Verbs in Use by Michael McCarthy and Felicity O'Dell</a></li>
                <li><a href="https://www.amazon.com/Complete-Guide-TOEFL-Test/dp/1413023110" target="_blank">The Complete Guide to the TOEFL Test by Bruce Rogers</a></li>
                <li><a href="https://www.amazon.com/English-Everyone-Beginner-Course-Book/dp/1465448684" target="_blank">English for Everyone: Level 1 Beginner, Course Book by DK</a></li>
            </ul>

            <h1>Buscas un libro en particular?</h1>
            <form id="formularioBusqueda">
                <div>
                    <label for="tituloLibro">Ingresa el ttulo del libro:</label>
                    <input type="text" id="tituloLibro" required/>
                </div>
                <button type="submit">Buscar Libro</button>
            </form>
            
            <div id="resultadoBusqueda"></div>
            <br></br>
            <iframe width="560" height="315" src="https://www.youtube.com/embed/nlawrM1gOYQ?si=whlbLlI8CsjpCHw5" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>
        </div>
    )
}
